export class Publisher {
    constructor() {
        this.sdkScript = "http://pubh5sdk-uat.iwgame.com/index.js";
        this.sdkType = "";
        this.publisherFuncList = {
            "init": window.x8Sdk.init,
            "login": window.x8Sdk.login,
            "pay": window.x8Sdk.pay,
            "submitLog": window.x8Sdk.submitData,
            "getDeviceInfo": window.x8Sdk.getDeviceInfo,
            "logout": window.x8Sdk.logout,
        };
    }
    /** 初始化 */
    init(data) {
        const { game_version, appkey } = data;
        return new Promise((resolve, reject) => {
            this.publisherFuncList.init({
                appkey: 'a657ba4a70244d83bd9105aa63ba0fea',
                game_version,
                callback: (res) => {
                    if (res.code == 200) {
                        resolve(true);
                    }
                    else {
                        reject(false);
                    }
                }
            });
        });
    }
    /** 登录 */
    login() {
        return new Promise((resolve, reject) => {
            this.publisherFuncList.login({
                callback: (res) => {
                    if (res.code == 200) {
                        resolve({
                            code: 200,
                            data: res.data
                        });
                    }
                }
            });
        });
    }
    /** 支付 */
    pay(params) {
        const { order_money_type = 'CNY', order_amount, pay_order_id, x8_order_id, product_id, product_name = '', product_desc = '', role_id, role_name = '', role_level = '', role_prof = '', role_sex = '', role_vip_level = '', server_id, server_name = '', } = params;
        return new Promise((resolve, reject) => {
            this.publisherFuncList.pay({
                info: {
                    order_money_type,
                    order_amount,
                    pay_order_id,
                    x8_order_id,
                    product_id,
                    product_name,
                    product_desc,
                    role_id,
                    role_name,
                    role_level,
                    role_prof,
                    role_sex,
                    role_vip_level,
                    server_id,
                    server_name,
                },
                callback: (res) => {
                    resolve(true);
                }
            });
        });
    }
    /** 登出 */
    logout() {
        this.publisherFuncList.logout({
            callback: () => {
            }
        });
        window.location.reload();
    }
}
